.gpt3__features-container__feature-tech {
    width: 100%;
}

.gpt3__features-container__feature-title-tech {
    margin-right: 2rem;
}

.gpt3__features-container__feature-title-tech h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.gpt3__features-container__feature-title-tech div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.gpt3__features-container_feature-text-tech p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

.gpt3__features-container_feature-text-tech ul {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}


.language_icon-tech {
    width: 50px;
    margin-left: 2rem;
}

@media screen and (max-width: 550px) {
    .gpt3__features-container__feature-title-tech h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .gpt3__features-container_feature-text-tech p {
        font-size: 12px;
        line-height: 20px;
    }

    .gpt3__features-container__feature-tech {
        margin: 1rem 0;
    }
}