.gpt3__blog {
    display: flex;
    flex-direction: column;    
}

.gpt3__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}

.gpt3__blog-heading h1 {
    font-size: 62px;
    line-height: 75px;
    font-family: var(--font-family);
    font-weight: 800;
}


.gpt3__blog-container_groupA {
    flex: 0.75;
    margin-right: 2rem;
}

.paginationBttns {
    width: 80%;
    height: 20px;
    margin-top: 2rem;
    list-style: none;
    display: flex;
    justify-content: center;
    align-self: center;
    font-family: 'Manrope', sans-serif;
    font-weight: bold;
    color:rgb(87, 87, 87)
}

.paginationBttns a {
    padding: 10px;
    margin: 8px;
    border-radius: 5px;
    border: 3px solid #aedcff;
    background-color: #ffffff;
    cursor: pointer;
}

.paginationBttns a:hover {
    background-color: #0f3d6ff7;
    color: white;
}

.paginationActive {
    color: white;
}

.paginationBttns li:hover {
    color: white;
}


.paginationActive a {
    background-color: #0f3d6ff7;
}

@media screen and (max-width: 990px) {
    .gpt3__blog-container {
        flex-direction: column-reverse;
    }


    .gpt3__blog-container_groupA {
        margin: 2rem 0;
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article  {
       width: 48%;
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article-image {
        height: 250px;
    }
}

@media screen and (max-width: 700px) {
    .gpt3__blog-container_groupB {
        grid-template-columns: repeat(1, 1fr);
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article  {
       width: 100%;
    }

    .gpt3__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

}